@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&family=Red+Hat+Display:wght@500;700;900&display=swap');


*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  outline: 0;
  text-decoration: none;
  list-style: none;
  border: none;
  font-family: 'Montserrat', sans-serif;;
}

:root {
  --dark-color:17%;
  --light-color:95%;
  --white-color:100%;
  --primary-color-hue: 252;
  --color-white: hsl(252, 30%, var(--white-color));
  --color-light: hsl(252, 30%, var(--light-color));
  --color-gray: hsl(252, 15%, 65%);
  --color-primary: hsl(var(--primary-color-hue), 75%, 60%);
  --color-secondary: hsl(252, 100%, 90%);
  --color-success: hsl(120, 95%, 17%);
  --color-danger: hsl(0, 95%, 65%);
  --color-dark: hsl(252, 30%, var(--dark-color));
  --color-black: hsl(252, 30%, 10%);
  --afterC: '0';
}

html {
  font-size: 80%;
}

body {
  background-color: var(--color-light);
  overflow-x: hidden;
  color: var(--color-dark);
  margin: auto;

  padding-left: 0px;
  padding-right: 0px;
  font-size: .8rem;
  font-family: "Poppins", sans-serif;

}


.profile {
  width: 0.3rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

.profile-pp {
  width: 6rem;

 
  overflow: hidden;
}


img {
  display: block;
  width: 100%;
}


.text-bold {
  font-weight: 500;
}

.text-muted {
  color: var(--color-gray);
}

.container {
  width: 100%;
  margin: auto;

}

.row {

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;

}

.row.center {
  justify-content: center;
}




/* footer start*/


.col-auto img {
  width: 150px;

}


.app-logo img {
  width: 120px;
  padding-bottom: 10px;
}

.copyright {
  text-align: center;
}

.footer hr {
  border: none;
  background: var(--color-gray);
  height: 1px;
}

/* footer ends */

/* main starts */
main{
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 75vh;
}

/* main ends */

